import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import {shou} from '../shou/shou'
import {view} from '../view/view'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path:"/",
      name:"view",
      component:view.shouye
      
    },
    {
      path:"/deng",
      name:"deng",
      component:view.deng
    },
    {
      path:"/zhu",
      name:"zhu",
      component:view.zhu
    },
    {
      path:"/book",
      name:"book",
      component:view.book
    },
    {
      path:"/center",
      name:"center",
      component:view.center
      
    },
    {
      path:"/daohan",
      name:"daohan",
      component:view.daohan
    },
    {
      path:"/shop",
      name:"shop",
      component:view.shop
    },
    {
      path:"/video",
      name:"video",
      component:view.video
    }
  ]
})



