			<el-carousel-item name="c"><el-image src="/static/fz/qipao3.jpg" style="height:200px;"></el-image></el-carousel-item>
		  </el-carousel>
			</el-col>
		  </el-row>
	  </el-col>
	    <el-col :span="6" :xs="24" style="border-box:box;padding:5px;">
		<el-row>
			<el-col :span="24" :xs="24"><h2><strong>排行榜</strong></h2></el-col>
		  </el-row>
		  <el-row>
			<el-col :span="5" :xs="5" :push="1"><el-tag type="primary"><span data-fz="my" @mouseenter="fza" @mouseleave="fzb" style="color:skyblue;">毛衣</span></el-tag></el-col>
			<el-col :span="5" :xs="5" :push="1"><el-tag type="primary"><span data-fz="dx"  @mouseenter="fza" @mouseleave="fzb" style="color:skyblue;">短袖</span></el-tag></el-col>
			<el-col :span="5" :xs="5" :push="1"><el-tag type="primary"><span data-fz="ck"  @mouseenter="fza" @mouseleave="fzb" style="color:skyblue;">长裤</span></el-tag></el-col>
			<el-col :span="5" :xs="5" :push="1"><el-tag type="primary"><span data-fz="qp"  @mouseenter="fza" @mouseleave="fzb" style="color:skyblue;">旗袍</span></el-tag></el-col>
		  </el-row>
		  <el-row>
			<el-col :span="24" :xs="24" v-if="fz=='my'">
				<el-row>
				  <el-col :span="24" :xs="24"><el-image src="/static/fz/my1.jpg" style="width:100px;height:100px;"></el-image></el-col>

				</el-row>
			</el-col>
			<el-col :span="24" :xs="24" v-if="fz=='dx'">
				<el-row>
				  <el-col :span="24" :xs="24"><el-image src="/static/fz/dx1.jpg" style="width:100px;height:100px;"></el-image></el-col>

				</el-row>
			</el-col>
			<el-col :span="24" :xs="24" v-if="fz=='ck'">
				<el-row>
				  <el-col :span="24" :xs="24"><el-image src="/static/fz/ck1.jpg" style="width:100px;height:100px;"></el-image></el-col>

				</el-row>
			</el-col>
			<el-col :span="24" :xs="24" v-if="fz=='qp'">
				<el-row>
				  <el-col :span="24" :xs="24"><el-image src="/static/fz/qipao.jpg" style="width:100px;height:100px;"></el-image></el-col>

				</el-row>
			</el-col>
		  </el-row>
		</el-col>

		  <el-col :span="6" :xs="24"   style="border-box:box;padding:5px;">
			<el-row>
			<el-col :span="24" :xs="24"><h2><strong>逛好店</strong></h2></el-col>
		  </el-row>
		  </el-col>
		    <el-col :span="6" :xs="6"  >
			<el-row>
			<el-col :span="24" :xs="24"><h2><strong>领劵中心</strong></h2></el-col>
		  </el-row>
			</el-col>
	</el-row>
  </div>
<!--颈部-->
	

</template>

<script>
import axios from 'axios'
import qs from 'qs'
export default {
	props:{
		a:{
			type:String
		},
		b:{
			type:String
		},
		c:{
			type:String
		}
	},
	mounted(){
		this.admin=this.$route.params.name
	},
	data(){
		return {
			admin:"",
			st:"a",
			he:20,
			url:"/static/fz/",
			list:[{index:'4',name:"dx1.jpg"},{index:'5',name:"dx2.jpg"},{index:'6',name:"dxnv2.jpg"},{index:'7',name:"dx3.jpg"}],
			img:[
				{index:'0',name:"ck1.jpg"},{index:'1',name:"ck2.jpg"},{index:'2',name:"ck3.jpg"},{index:'3',name:"dxnv3.jpg"}
			],
			goods:"jx",
			time:new Date().toLocaleString(),
			car:"a",
			index:1,
			tabs:"first",
			user:"/static/img/cx.jpg",
			form:{
				sou:""
			},
			msg:"",
			tu:"@/static/img/beij.jpg",
			t:"",
			fz:"my",
			sel:"",
			x:"",
			xz:"",
			
			item:[
				{
					label:"旗袍",
					value:"旗袍"
				},
				{
					label:"长裤",
					value:"长裤"
				},
				{
					label:"毛衣",
					value:"毛衣"
				},
				{
					label:"短袖",
					value:"短袖"
				}

			]
		}
	},
	created(){
		let n=-1
		this.t=setInterval((x) => {
			n=n+1
			if(n==this.list.length){
				n=0
			}
			this.img.shift();
			this.img.push(this.list[n])
			
			//console.log(s)
			//console.log(this.img)
		}, 4000);
		this.$store.dispatch("search")			
	},
	destroyed(){
		clearInterval(this.t);
		
	},
	methods:{
		it:function(value){
				this.form.sou=value
		},
		handleSelect(value){
			console.log(value)
		},
		querySearch(query,cd){
			console.log(123)
					
					
				
				const text=this.$store.state.text
					
					 var results = query ?text.filter((restaurant) => {//过滤函数变量数组类数字返回操作值
          if(restaurant.value.indexOf(query)===0){//类数组里面必须要有一个value自定义属性才能获取变量
			return restaurant//在生命周期created加载的时候是先数据然后再是方法
			//
          }//类数组过滤索引内容把符合条件的索引留下
         // console.log(restaurant)
         // return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0;
        }) : text;
        // 调用 callback 返回建议列表的数据
        
        cd(results)//返回对象js里面数组是对象数据类型是object
			//生命周期
		},
		sou:function(){
			this.$store.state.search=this.form.sou
			
		},
		push(){
			this.$router.push({
				name:"deng"
			})
		},
		get:function(){
			const admin={
				params:{//get提交
					user:"liu",
				pass:"yong"
				}
			}
			const header={
				headers:{"Content-Type":"application/json"}
			}
			axios.get("http://localhost:8081/",admin,header).then(response=>{this.msg=response.data}).catch((err)=>{console.log(err)})
		},
		post:function(){
			const admin={
				user:"liu",
				pass:"yong"
			}
			const header={
				headers:{
					"Content-Type":"application/x-www-form-urlencoded"
				}
			}
			axios.post("http://localhost:8081/",qs.stringify(admin),header).then(response=>{this.msg=response.data}).catch((err)=>{console.log(err)})
		},
		del:function(){

		},
		put:function(){

		},
		show:function(e){
			console.log(e)
			this.index=e.target.dataset.set;
			
			e.target.style.color="red"
		},
		color:function(e){
			e.target.style.color="black"
		},
		showt:function(e){
			console.log(e)
			this.goods=e.target.dataset.set;
			
			e.target.style.color="red"
		},
		colort:function(e){
			e.target.style.color="black"
		},
		fza:function(e){
			this.fz=e.target.dataset.fz
			e.target.style.color="salmon"
		},
		fzb:function(e){
			e.target.style.color="skyblue"
		}

	}
}
</script>




